  font-size: 12px;
  font-weight: 500; }

.schedule-available {
  background: #1aab60;
  height: 25px;
  margin: 0px 1px; }

.call-schedule-panel {
  min-height: 265px; }

.table-sickcalls th:nth-child(4),
.table-sickcalls td:nth-child(4) {
  text-align: center; }

.sick-call-inmate-details {
  display: none; }

.table-notifications .col-time {
  width: 40%; }

.collapse-panel:nth-of-type(1) {
  margin-top: 2rem; }

.collapse-panel [class*=toggle-]:after {
  position: absolute; }

.collapse-panel .panel-body > [class*=col-] {
  padding-left: 0%;
  padding-right: 0%; }
  .collapse-panel .panel-body > [class*=col-] ~ [class*=col-]:not(.collapse-panel) {
    padding-left: 15px;
    padding-right: 15px; }

.collapse-panel .actions {
  padding-right: 0%; }
  .collapse-panel .actions .btn[class*=btn-] {
    height: 25px;
    line-height: 20px;
    margin-top: -0.11em;
    transition: background-color 0.2s;
    width: 25px; }
    .collapse-panel .actions .btn[class*=btn-]:not(:only-of-type) {
      float: right; }
    .collapse-panel .actions .btn[class*=btn-]:only-of-type {
      margin-left: 10px; }
    .collapse-panel .actions .btn[class*=btn-] ~ .btn {
      margin-right: 5px; }
    .collapse-panel .actions .btn[class*=btn-][class*=pencil] {
      background-color: transparent;
      border: 1px solid #aaa;
      transition: color 0.2s, border 0.2s; }
      .collapse-panel .actions .btn[class*=btn-][class*=pencil]:hover {
        border: 1px solid #389924;
        color: #389924; }

.collapse-panel .collapse > .row:not(:last-child),
.collapse-panel .collapsing > .row:not(:last-child) {
  padding-bottom: 2.5px; }

.collapse-panel .collapse > .row ~ *,
.collapse-panel .collapsing > .row ~ * {
  padding-top: 2.5px; }

.collapse-panel [class*=icon-status-] {
  font-size: 12px;
  line-height: 20px;
  margin-right: 5px; }
  .collapse-panel [class*=icon-status-].icon-status-pending {
    color: #f26122; }
  .collapse-panel [class*=icon-status-].icon-status-approved {
    color: #3fae29; }
  .collapse-panel [class*=icon-status-].icon-status-rejected {
    color: #c0392b; }

/* stepper*/
.btn-stepper {
  background: #f9f9f9;
  border: 1px solid #c9c9c9;
  border-radius: 0;
  color: #c9c9c9;
  height: 2.143rem;
  padding: 0;
  transition: color 0.2s, border-color 0.2s;
  width: 2.143rem; }
  .btn-stepper:hover {
    border: 1px solid #666;
    color: #666; }

.left-col {
  margin-top: 30px; }

img.dl {
  height: 60px;
  width: auto; }

.monitor-multisession-checklist {
  height: calc(100% - 35px);
  max-height: calc(100vh - 550px);
  min-height: calc(438px - 35px);
  overflow-x: auto; }
